export function authorization_status(trello) {
	console.log("Running authorization status")
	return trello.get("member", "private", "access_token")
		.then(x => {
			return { authorized: false };
		})
}

export function show_authorization(trello) {
	const goodreads_window = window.open("https://goodreads.com");
	if (!goodreads_window || goodreads_window.closed || typeof goodreads_window.closed == 'undefined') {
		alert("Please enabled pop-ups");
	} else {
		goodreads_window.focus();
	}
}